  padding-top: 0.5%;
  padding-bottom: 0.5%;
  lost-center: 1140px 1% flex;
  margin-top: 0.5%;
  margin-bottom: 0.5%;
}

div { 
  lost-column: 1/2 2 2% flex;
  margin-top: 1%; 
  margin-bottom: 1%;

}

.header {
  height: 15vh;
}



  righthead {
    lost-flex-container: row;
    lost-column: 3/4 2 2% flex;

     
    margin-bottom: 1%;
    margin-top: 1%; 
     
     
    telpanel {
      justify-content: center;
      lost-flex-container: row;
      lost-column: 1/1 1 1% flex;

      div {
        text-align: center;
        lost-column: 1/3 3 3% flex;
        align-self: center;
          
      }
      
      margin-bottom: 1%;
      @util border-bottom-radius(0.6em);
    }

    headmenu {
      lost-column: 1/1 1 1% flex;
      margin-top: 1%;
      @util border-top-radius(0.6em);
      ul {
        list-style: none;    
        text-align: center;
        padding: 0;
        margin: 0;  
        li {
          display: inline-block;
        }
      }
  }
}

 

/* helpers/align.css */

.align {
  align-items: center;
  display: flex;
  flex-direction: row;
  justify-content: center;
}


 

/* modules/anchor.css */

a {
  outline: 0;
  text-decoration: none;
  transition: color 0.3s, background-color 0.3s;
}

/* modules/navigation.css */

.navigation ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.navigation > ul {
  display: flex;
}

.navigation li {
  position: relative;
}

.navigation li:hover > ul {
  display: block;
}

.navigation a {
  background-color: #406a82;
  color: inherit;
  display: block;
  padding: 0.25em 0.5em;
}

.navigation a:focus,
.navigation a:hover {
  background-color: #365e75;
}

.navigation__dropdown {
  display: none;
  position: absolute;
}

.navigation__dropdown--left {
  left: 0;
  top: 100%;
}

.navigation__dropdown--right {
  left: 100%;
  top: 0;
}



